import React, {Component} from 'react';
import './Detail.scss'

class Detail extends Component {
   constructor(props) {
      super(props);
      this.state = {
         item: props.location.state
      }
      window.scrollTo(0,0);
   }
   aaa(e){
      e.target.src = this.state.item.cover.origin;
   }
   render() {
      return (
         <div className="detail">
            <img src={'../../styles/aaa.jpg'} onLoad={this.aaa.bind(this)}/>
            <div className="box">
               <h2></h2>
               <p>
                  <span></span>
                  <span>{this.state.item.name}</span>
               </p>
               <p>
                  <span></span>
                  <span></span>
               </p>
               <p>
                  <span></span>
                  <span></span>
               </p>
               <p>
                  <span></span>
                  <span></span>
               </p>
               <p>
               <span></span>
               <span></span>
               </p>
               <p>
                  <span></span>
                  <span></span>
               </p>
               <div className="text">
                   ! ! ! ! !
                  ! ! ! ! ! !
                  ! ! ! ! ! !
                  ! ! !
               </div>
               <span></span>
            </div>
         </div>
      )
   }
}

export default Detail;


