import React, { Component } from 'react'
import {NavLink} from 'react-router-dom'
import './Drawer.scss'

class Drawer1 extends Component {
   render() {
      return (
         <ul >
            <li>
               <NavLink to="/" onClick={()=>{this.props.toggle();window.title1=''}}>
                  <i></i>
                  <span className="iconfont icon-arrow-right"></span>
               </NavLink>
            </li>
            <li>
               <NavLink to="/list" onClick={()=>{this.props.toggle();window.title1=''}}>
                  <i></i>
                  <span className="iconfont icon-arrow-right"></span>
               </NavLink>
            </li>
            <li>
               <NavLink to="/cinema" onClick={()=>{this.props.toggle();window.title1=''}}>
                  <i></i>
                  <span className="iconfont icon-arrow-right"></span>
               </NavLink>
            </li>
            <li>
               <NavLink to='/user' onClick={()=>{this.props.toggle()}}>
                  <i></i>
                  <span className="iconfont icon-arrow-right"></span>
               </NavLink>
            </li>
            <li>
               <NavLink to="/card" onClick={()=>{this.props.toggle();window.title1=''}}>
                  <i></i>
                  <span className="iconfont icon-arrow-right"></span>
               </NavLink>
            </li>
         </ul>
      )
   }
}

export default Drawer1;



