import React, { Component } from 'react';
import './Login.scss'

class Login extends Component {
   constructor(props){
      super(props);
      this.state = {
         text: '',
         pwd: '',
         show: false,
         info: ''
      }
      window.title1 = '';
   }
   handleChange({target}){
      this.setState({
         [target.name]: target.value
      })
   }
   Query(){
      let text = this.state.text.trim();
      let pwd  = this.state.pwd.trim();
      
      if(!text.length){
         this.setState({info: '', show: true})
         return;
      }
      if(text!='13888888888'){
         this.setState({info: '', show: true})
         return;
      }
      if(!pwd.length){
         this.setState({info: '', show: true})
         return;
      }
      if(pwd!='888'){
         this.setState({info: '',show: true})
         return;
      }
      sessionStorage.setItem('isLogin',1)
      this.setState({text: '',pwd: ''})
      this.props.history.push('/user');
      window.title1 = '';
   }
   render(){
      return (
         <div className="login">
            <input type="text" autoComplete='off' placeholder="" name="text" value={this.state.text} onChange={this.handleChange.bind(this)}/>
            <input type="password" autoComplete='off' placeholder="" name="pwd" value={this.state.pwd} onChange={this.handleChange.bind(this)}/>
            <b>{this.state.show ? this.state.info : ''}</b>
            <div className='btn'>
               <span onClick={this.Query.bind(this)}></span>
            </div>
         </div>
      )
   }
}

export default Login;


