import React, { Component } from 'react';
import './Item.scss'
import {Link} from 'react-router-dom'


class Item extends Component {
   aaa(e){
      e.target.src = this.props.item.cover.origin;
   }
   render() {
      return (
         <div className='item'>
           <Link to={{pathname:'/detail',state:this.props.item}} onClick={()=>{window.title1=this.props.item.name}}>
            <div className="img">
               <img src={'../../styles/aaa.jpg'} onLoad={this.aaa.bind(this)} width="100%"/>
            </div>
            <div className="title">
               <h2>
                  <p>{this.props.item.name}</p>
                  {
                     this.props.flag=='nowplay'?
                     <p className="last">
                        {this.props.item.cinemaCount}&nbsp;&nbsp;
                        {this.props.item.watchCount}
                     </p>
                     : null
                  }
               </h2>
               {
                  this.props.flag=='nowplay' ?
                  <h3>{this.props.item.grade}</h3> :
                  <h4></h4>
               }
            </div>
            </Link>
         </div>
      )
   }
}

export default Item;


